/* -----------------Chatbot CSS----------------- */
/* optimise chatbot sizing for Pixel 2 device */
.rsc {
  position:fixed;
  bottom:100px;
  width:93%;
}

.rsc-container {
  height: 620px;
}

.rsc-content.sc-gZMcBi.fBGGBn {
  height: 560px;
  overflow-y: scroll;
}

/* align user options to the right so that users know that they need to pick an option */
.rsc-os-option {
list-style-type: none;
padding: 0;
margin: 3px;
text-align: right;
float: right;
}

.rsc-os-option-element {
  background-position: 100% .4em !important;
  padding-right: .6em !important;
  color:white !important;
} 

/* added bottom margin for aunty's second consecutive message */
.rsc-ts.rsc-ts-bot.sc-dnqmqq.efROPc {
  margin-bottom: .4em;
}

/* users options bubble */
.rsc-os-option-element.sc-bxivhb.cBYVVs {
  background:gray;
  color:white;
}


/* users chat bubble */
.rsc-ts-bubble.sc-bZQynM.iOCYje {
  background:gray;
  color:white;
}

/* aunty bubble confirming the time itinerary */
.rsc-ts-bubble.sc-bZQynM.ksTzOQ, .rsc-ts-bubble.sc-bZQynM.iqYXai {
  margin: 0;
}

/* remove chat avatars */
.cwuCQv, .eijbEu {
  display:none;
}

/* container to expannd the chat view */
.rsc-container.sc-iwsKbI.ijcYgp {
width: 100%;
margin-top:2px;
}

